html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  margin-top: 9.7rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  main {
    margin-top: 14.4rem;
  }
}
@media (min-width: 992px) {
  main {
    margin-top: 34rem;
  }
}

body,
p {
  text-align: justify;
  font-size: 1.2rem;
}
@media (min-width: 480px) {
  body,
p {
    font-size: 1.35rem;
  }
}
@media (min-width: 768px) {
  body,
p {
    font-size: 1.5rem;
  }
}
@media (min-width: 992px) {
  body,
p {
    font-size: 1.75rem;
  }
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.accordion-button {
  font-size: 1.35rem;
}
@media (min-width: 480px) {
  .accordion-button {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  .accordion-button {
    font-size: 1.65rem;
  }
}
@media (min-width: 992px) {
  .accordion-button {
    font-size: 2rem;
  }
}

h1,
h2,
h3 {
  text-align: center;
  color: #226e93;
  font-family: var(--bs-body-font-family);
}

h1 {
  font-size: 2.25rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2rem;
}
@media (min-width: 480px) {
  h2 {
    font-size: 2.25rem;
  }
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.5rem;
  }
}
@media (min-width: 992px) {
  h2 {
    font-size: 2.75rem;
  }
}

h3 {
  font-size: 1.75rem;
}
@media (min-width: 480px) {
  h3 {
    font-size: 2.05rem;
  }
}
@media (min-width: 768px) {
  h3 {
    font-size: 2.3rem;
  }
}
@media (min-width: 992px) {
  h3 {
    font-size: 2.55rem;
  }
}

img {
  width: 100%;
}

.imagen {
  margin-left: auto;
  margin-right: auto;
}

.header {
  background-color: transparent;
}

.navbar-brand {
  display: inline-block;
  line-height: 1;
  max-width: 60%;
  outline: none;
  overflow: hidden;
  padding: 16px 0px;
  text-decoration: none;
  text-overflow: ellipsis;
  font-size: 13.5pt;
  font-family: "PT Sans", sans-serif;
  white-space: nowrap;
  font-weight: 700;
}
@media (min-width: 1200px) {
  .navbar-brand {
    font-size: 16pt;
  }
}

.float-right {
  display: none;
}
@media (min-width: 992px) {
  .float-right {
    display: flex;
  }
}

@media (min-width: 992px) {
  .navbar-nav li {
    padding-right: 0.4rem;
  }
}
@media (min-width: 1200px) {
  .navbar-nav li {
    padding-right: 2rem;
  }
}

@media (min-width: 992px) {
  .dropdown-menu li {
    position: relative;
  }
  .dropdown-menu li:hover {
    background-color: #226e93;
  }

  .nav-item .submenu {
    display: none;
    position: absolute;
    left: 95%;
    top: -5px;
  }

  .menu {
    font-size: 10pt;
    font-family: "PT Sans", sans-serif;
  }

  .nav-item .submenu-left {
    right: 100%;
    left: auto;
  }

  .dropdown-menu > li:hover > .submenu {
    display: block;
  }
}
.dropdown-item {
  background-color: transparent;
}
.dropdown-item:hover {
  background-color: #212529;
}
@media (min-width: 1200px) {
  .dropdown-item {
    font-size: 1.4rem;
    padding-top: 1rem;
    padding-bottom: 0.5rem;
    padding-left: 2rem;
  }
}

.nav-sb a:visited {
  background-color: #212529;
}

.dropdown-menu .dropdown-menu {
  margin-left: 0.7rem;
  margin-right: 0.7rem;
  margin-bottom: 0.5rem;
}

.container-fluid {
  display: flex;
  justify-content: space-between;
}

.navbar-collapse {
  flex-grow: 0.5;
}

.dropdown-menu[data-bs-popper] {
  top: 4.7rem;
}
@media (min-width: 1200px) {
  .dropdown-menu[data-bs-popper] {
    top: 5rem;
  }
}

.navbar-toggler {
  display: block;
}
@media (min-width: 992px) {
  .navbar-toggler {
    display: none;
  }
}

.sidebar {
  display: block;
}
@media (min-width: 992px) {
  .sidebar {
    display: none;
  }
}

.sidebar-text {
  display: block;
  font-size: 1rem;
  max-width: 90%;
}
@media (min-width: 480px) {
  .sidebar-text {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) {
  .sidebar-text {
    font-size: 1.5rem;
  }
}
@media (min-width: 992px) {
  .sidebar-text {
    display: none;
  }
}

.navbar-text {
  display: none;
}
@media (min-width: 992px) {
  .navbar-text {
    display: block;
  }
}

.offcanvas {
  overflow-y: auto;
}

.offcanvas-start {
  width: fit-content;
}

.offcanvas-header,
.offcanvas-body {
  padding: 0 2.5rem 0 2rem;
  font-size: 1.25rem;
  overflow-y: unset;
}
@media (min-width: 480px) {
  .offcanvas-header,
.offcanvas-body {
    font-size: 1.5rem;
  }
}

.offcanvas-header {
  padding-top: 2rem;
}
.offcanvas-header .btn-close {
  margin-right: 2rem;
}

.offcanvas-title {
  font-size: 1.5rem;
}
@media (min-width: 480px) {
  .offcanvas-title {
    font-size: 1.75rem;
  }
}

.sidebar-menu {
  padding-bottom: 0.5rem;
}
.sidebar-menu:first-of-type {
  padding-top: 2rem;
}

.sidebar-last {
  padding-bottom: 2rem;
}

.sidebar-drop {
  margin-top: 2rem;
}

.btn-close {
  padding-right: 1rem;
}

.menu-sb {
  border: 0;
  font-size: 1.25rem;
}
.menu-sb li {
  padding-top: 1rem;
}

.dropdown-item:focus,
.dropdown-item:hover,
.nav-link:focus,
.nav-link:hover {
  color: #226e93;
}

.dropdown-item:focus,
.dropdown-item:hover {
  background-color: unset;
}

.nav-sb a,
.navbar-dark .navbar-nav .nav-link {
  color: #ffffff;
  transition: color 0.5s;
}
.nav-sb a:hover,
.navbar-dark .navbar-nav .nav-link:hover {
  color: #226e93;
}
@media (min-width: 1200px) {
  .nav-sb a,
.navbar-dark .navbar-nav .nav-link {
    font-size: 1.75rem;
  }
}

.dropdown-item-opt {
  display: contents;
}

.left-pad {
  padding-left: 1rem;
}

.nav-item-close-news {
  position: relative;
}
.nav-item-close-news a {
  position: absolute;
  left: -3.7rem;
}
@media (min-width: 1200px) {
  .nav-item-close-news a {
    left: -2.8rem;
  }
}

.blank-space-news {
  width: 3.7rem;
}
@media (min-width: 1200px) {
  .blank-space-news {
    left: -2.8rem;
  }
}

.nav-item-close-research {
  position: relative;
}
.nav-item-close-research a {
  position: absolute;
  left: -6rem;
}
@media (min-width: 1200px) {
  .nav-item-close-research a {
    left: -5.5rem;
  }
}

.blank-space-research {
  width: 6rem;
}
@media (min-width: 1200px) {
  .blank-space-research {
    width: 5.5rem;
  }
}

@media (min-width: 992px) {
  .dropdown-menu-news {
    left: -5rem !important;
  }
}

@media (min-width: 992px) {
  .dropdown-menu-research {
    left: -7rem !important;
  }
}

@media (min-width: 992px) {
  .dropdown-submenu-research {
    left: 13.9rem !important;
  }
}
@media (min-width: 1200px) {
  .dropdown-submenu-research {
    left: 17.1rem !important;
  }
}

/* The hero image */
.hero-image {
  z-index: -1;
  /* Use "linear-gradient" to add a darken background effect to the image (photographer.jpg). This will make the text easier to read */
  background-image: url("../../build/img/hero/hero2.webp");
  /* Set a specific height */
  height: 15rem;
  /* Position and center the image to scale nicely on all screens */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .hero-image {
    height: 20rem;
  }
}
@media (min-width: 992px) {
  .hero-image {
    height: 40rem;
  }
}

/* Place text in the middle of the image */
.hero-text {
  font-weight: bold;
  color: #ffffff;
  font-size: 2.5rem;
  text-align: center;
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 992px) {
  .hero-text {
    font-size: 5rem;
  }
}
@media (min-width: 992px) {
  .hero-text {
    top: 55%;
  }
}

main {
  margin-right: 2rem;
  margin-left: 2rem;
}
@media (min-width: 480px) {
  main {
    margin-right: 5rem;
    margin-left: 5rem;
  }
}
@media (min-width: 1200px) {
  main {
    margin-right: 10rem;
    margin-left: 10rem;
  }
}

.home-div {
  margin-top: 2.5rem;
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .home-left .imagen {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 992px) {
  .home-left .imagen {
    width: 70%;
  }
}

.home-div .imagen {
  padding: 1rem 1rem 3rem 1rem;
}
@media (min-width: 480px) {
  .home-div .imagen {
    padding: 0 13rem 3rem 13rem;
  }
}
@media (min-width: 992px) {
  .home-div .imagen {
    padding: 1rem 25rem 5rem 25rem;
  }
}
@media (min-width: 1200px) {
  .home-div .imagen {
    padding: 1rem 30% 5rem 30%;
  }
}

.accordion-image {
  display: flex;
  justify-content: center;
  margin: 3rem 0;
}
.accordion-image .logo-exo {
  width: 10rem;
}
@media (min-width: 1200px) {
  .accordion-image .logo-exo {
    width: 15rem;
  }
}
@media (min-width: 480px) {
  .accordion-image .logo-3D,
.accordion-image .logo-humanoid {
    width: 22rem;
  }
}
@media (min-width: 768px) {
  .accordion-image .logo-3D,
.accordion-image .logo-humanoid {
    width: 25rem;
  }
}
@media (min-width: 1200px) {
  .accordion-image .logo-3D,
.accordion-image .logo-humanoid {
    width: 35rem;
  }
}

@media (min-width: 1200px) {
  .accordion {
    width: 90rem;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 992px) {
  .accordion-body {
    display: flex;
  }
  .accordion-body .accordion-image {
    padding: 0 2rem 0 2rem;
  }
  .accordion-body .accordion-3D {
    order: -1;
  }
}

@media (min-width: 768px) {
  .home-location {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 5rem;
  }
